"use client";

import React from "react";
import { useTheme } from "next-themes";

interface ModuleCardProps {
  title: string;
  status: "completed" | "active" | "locked";
}

const ModuleCard: React.FC<ModuleCardProps> = ({ title, status }) => {
  const { theme } = useTheme();

  const getStyles = () => {
    switch (status) {
      case "completed":
        return "bg-neutral-100 text-neutral-800 border-2 border-primary-light";
      case "active":
        return "bg-primary text-white";
      case "locked":
        return "bg-neutral-100 text-neutral-400 border-2 border-neutral-300";
      default:
        return "";
    }
  };

  const getIconSrc = () => {
    switch (status) {
      case "completed":
        return theme === "dark"
          ? "/assets/dark/check.svg"
          : "/assets/light/check.svg";
      case "active":
        return theme === "dark"
          ? "/assets/dark/lock-open.svg"
          : "/assets/light/lock-open.svg";
      case "locked":
        return theme === "dark"
          ? "/assets/dark/lock.svg"
          : "/assets/light/lock.svg";
      default:
        return "";
    }
  };

  return (
    <div
      className={`flex items-center justify-between p-4 rounded-lg shadow-sm ${getStyles()}`}
    >
      <span className="p14 font-medium">{title}</span>
      <img src={getIconSrc()} alt={status} className="w-5 h-5" />
    </div>
  );
};

export default ModuleCard;
